.end-container{
    display: grid;
    grid-template-rows: 33% 33% 33%;
    grid-column-gap: 100px;
    align-content: center;
    margin-top: 10%;
}
.end-btn{
    background-color: blue;
    width: 30%;
    margin: 0 auto;
    font-size: 20px;
    padding: 20px;
    cursor: pointer;
    border: 2px solid black;
    border-radius: 10px;
    color: white;
}
